import Title from "components/title";

function Experience({
  companyName,
  children,
  endDate,
  location,
  startDate,
  title,
}) {
  return (
    <div className="mb-5">
      <div className="flex flex-column-xs">
        <Title title={title} />
        <div className="flex-3 flex-xs-4">
          <small className="text-italic mr-4">
            {startDate} - {endDate}
          </small>
          <strong className="text-large">
            {companyName}, {location}
          </strong>
          <div className="mt-2">{children}</div>
        </div>
      </div>
    </div>
  );
}

export default Experience;
